// ICON STYLES =============================================
// =========================================================
@font-face {
    font-family: 'icomoon';
    src:    url('/assets/img/icons/icomoon.eot?gx80it');
    src:    url('/assets/img/icons/icomoon.eot?gx80it#iefix') format('embedded-opentype'),
            url('/assets/img/icons/icomoon.ttf?gx80it') format('truetype'),
            url('/assets/img/icons/icomoon.woff?gx80it') format('woff'),
            url('/assets/img/icons/icomoon.svg?gx80it#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

